<template>
    <div class="login">
        <elm-header :fatherName="fatherName"></elm-header>
        <form>
            <template v-if="parameter=='register'">
                <mt-field label="用户名" placeholder="请输入用户名" v-model="username"></mt-field>
                <mt-field label="密码"  placeholder="请输入密码" type="password" v-model="password"></mt-field>
                <mt-field label="手机"  placeholder="请输入您的手机号" type="tel" v-model="phone"  @blur.native.capture="checkPhone(phone)"></mt-field>
                <mt-field label="验证码" :state="state" v-model="captcha">
                    <img src="../../static/img/Verification-Code.png" height="45px" width="100px">
                </mt-field>
            </template>
            <template v-if="parameter=='signin_pwd'">
                    <mt-field label="用户名" placeholder="请输入用户名" v-model="username"></mt-field>
                    <mt-field label="密码" placeholder="请输入密码" type="password" v-model="password"></mt-field>
                    <mt-field label="验证码" v-model="captcha">
                        <img src="../../static/img/Verification-Code.png" height="45px" width="100px">
                    </mt-field>
            </template>
            <template v-if="parameter=='signin_phone'">
                <mt-field label="手机" placeholder="请输入您的手机号" type="tel" v-model="phone"></mt-field>
                <mt-field label="验证码" v-model="captcha">
                    <img src="../../static/img/Verification-Code.png" height="45px" width="100px">
                </mt-field>

            </template>
        </form>
        <div class="login-submit">
                <mt-button v-if="parameter=='register'" type="primary" size="large" @click.native.capture="register" >注册</mt-button>
                <mt-button v-else type="primary" size="large"  @click.native.capture="login">登录</mt-button>
        </div>
    </div>
</template>

<script>

export default{
    name:'login',
    data () {
        return {
            login_phone:false,
            fatherName:null,
            parameter:null,
            username:null,
            password:null,
            captcha:null,
            phone:null,
            state:null,
        }
    },
    created() {
        let logoin_parameter=this.$route.params.login_parameter;
        this.parameter=logoin_parameter;
        if(logoin_parameter==="register"){
            this.fatherName="注册";
        }else{
            this.fatherName="登录";
        }
    },
    mounted() {

    },
    watch: {
        $route:function(to,from){
            this.parameter = to.params.login_parameter;
            //console.log(this.parameter)
            if (this.parameter === "register") {
                this.fatherName = "注册";
            }else{
                this.fatherName = "登录";
            }
            //console.log(this.fatherName)
        },
        username:function(newval,oldval){
            if(newval.length>20){
                alert("你输入用户名太长了，不能超过10个字符哦！");
            }
        },
        phone:function(newval){

        }

    },
    methods: {
        //mint-ui input的事件使用@blur.native.capture="";
        checkPhone:function(val){
            let re=/^\s*1[3,4,5,7,8]\d{9}\s*$/;
            if(!re.test(val)){
                alert("请输入正确的手机号码！");
                this.phone=null;
            }
        },
        register:function(){
            if(this.username && this.password && this.phone && this.captcha ){
                alert("注册成功")
                this.$router.push("/home")
                let userInfo={
                    "username":this.username,
                    "password":this.password,
                    "phone":this.phone

                }
                sessionStorage.setItem("userInfo",JSON.stringify(userInfo))
            }else{
                alert("请正确填写表单信息")
            }
        },
        login:function(){
            let userInfo=JSON.parse(sessionStorage.getItem("userInfo"));
            console.log(userInfo)
            console.log(this.username,this.password)
            if(this.username==userInfo.username && this.password==userInfo.password){
                alert("登录成功")
                this.$router.push({
                    path:"/home",
                    query:{
                        state:true
                    }
                })
            }else{
                alert("请检查您的用户名和密码是否正确！")
            }
        }
    }
}

</script>

<style scoped>
.login form{
    margin-top:0.2rem;
    background:#fff;
    padding:0 0.3rem 
}
.login-submit{
    margin-top:1.4rem;
}

</style>


